@media (max-width: 600px) {
  .pixel-title {
    font-size: 1.2rem;
  }

  .pixel-nav {
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
  }

  .contact-links a {
    font-size: 0.6rem;
  }

  .pixel-heading {
    font-size: 0.8rem;
  }
}

@keyframes catWalkBanner {
  0% {
    left: 100%;
    transform: scaleX(-1); /* face left */
  }
  100% {
    left: -80px; /* fully off-screen */
    transform: scaleX(-1);
  }

}

@keyframes catWalkNav {
  0% {
    left: 100%;
    transform: scaleX(-1); /* face left */
  }
  100% {
    left: -80px;
    transform: scaleX(-1);
  }
}